import { useFetcher, useLoaderData, useTransition } from "remix";
import { getArtList } from "~/services/art";
import { Outlet } from "remix";
export let loader = async () => {
  return await getArtList();
};

export default function Art() {
  let data = useLoaderData();
      const transition = useTransition();


  return (
    <div>
      {data.map((e, k) => {
        return <span key={k}> {e.title} </span>;
      })}
      <Outlet />
      <div>{transition.state}</div>;
    </div>
  );
}
